import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Link, Switch, Redirect} from 'react-router-dom'
import MenuIcon from './menuIcon'
import Home from '../views/home'
import Projects from '../views/projects'

var width = (window.innerWidth > 0) ? window.innerWidth : window.screen.width;

var thisIsMyCopy = `<ul className="navUL">
<li><Link to={'/'}>Home</Link></li>
<li><a id="abtLink" href="#about">About</a></li>
<li><Link to={'/projects'}>Projects</Link></li>
<li><a href="#footer">Contact Us</a></li>
</ul>`

var ulLink = <div></div>

window.onresize = function(e){
  width = (window.innerWidth > 0) ? window.innerWidth : window.screen.width;
  if(width > 768){
    ulLink = <div className="content" dangerouslySetInnerHTML={{__html: thisIsMyCopy}}></div>
  }else{
     ulLink =  <div></div>
  }
}


export default class navigation extends Component {
  constructor(){
    super();
    this.state={
      links: <MenuIcon />
    }
  }

  updateNav(){
    if(window.innerWidth > 768){
      this.setState({
        links: <ul className="navUL">
        <li><Link to={'/'}>Home</Link></li>
        <li><a id="abtLink" href="#about">About</a></li>
        <li><Link to={'/projects'}>Projects</Link></li>
        <li><a href="#footer">Contact Us</a></li>
      </ul>
      })
    }else{
      this.setState({
        links: <MenuIcon />
      })
    }
  }

  componentDidMount() {
    this.updateNav();
    window.addEventListener("resize", this.updateNav.bind(this));
  }

  render() {
    return (
      <div style={{"height":"100%"}}>
        <Router>
            <div className="navbar nav">
                <div className='container'>
                    <div className="logo">
                        <a href="/"><h2>devBean</h2></a>
                    </div>
                    {this.state.links}
                </div>     
            </div>
       
            <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/projects" component={Projects} />
                <Redirect to="/" />
            </Switch>
        </Router>  
     
      </div>
    )
  }
}
